.wp-ml-switcher {
  position: relative;
  padding: 5px 8px 5px 7px;
}

.wp-ml-switcher ul {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  list-style: none;
  margin: 0;
  padding: 5px 8px 5px 7px;
  z-index: 100;
}

.wp-ml-switcher:hover ul {
  display: block;
}

.wp-ml-switcher:hover ul li {
  line-height: 1;
}

.wp-ml-switcher img {
  width: 20px;
  max-width: 20px;
  height: auto;
}




